@charset "UTF-8"

@import bootstrap/functions

@import variables

@import bootstrap/bootstrap

@import common/icons

body
  min-height: 100vh
  padding-top: 98px

+media-breakpoint-up(lg)
  .raff-sticky-ad
    position: sticky
    top: 110px

.loh
  display: inline-block
  width: 24px
  height: 24px 
  margin: 0
  padding: 0
  position: relative
  cursor: pointer
  background: transparent url('../img/basket-highlight.png') no-repeat center / cover
  +hover-focus
    &:before
      opacity: 0
  &:before
    content: ""
    display: block
    position: absolute
    left: 0
    top: 0
    right: 0
    bottom: 0
    transition: $transition-fade
    background: transparent url('../img/basket-dark.png') no-repeat center / cover

[ng\:cloak], [ng-cloak], .ng-cloak
  display: none !important

.lh-0
  line-height: 0 !important
.lh-1
  line-height: 1 !important

.row-full
  width: 100vw
  position: relative
  margin-left: -50vw
  left: 50%

.bg-2
  background: $body-bg url('../img/bg.jpg') no-repeat center top 50px / cover

.bg
  background: $body-bg url('../img/bg.jpg') no-repeat center top / cover

\:focus
  outline: 0 !important

.footer-nav
  font-size: 0.875rem
  font-family: $font-family-source-sans
  a
    color: $white
  +media-breakpoint-up(sm)
    font-size: 1rem
  +media-breakpoint-up(md)
    font-size: 1.125rem
#header
  border-top: $border-width solid theme-color('theme')
  box-shadow: $box-shadow-sm

.modal-content
  border: 0
  box-shadow: 0 2px 40px 0px rgba($black, 0.19)
textarea
  resize: none
.box
  margin: 0 0 space('g')
  background: $white
  box-shadow: $box-shadow

  &-body
    padding: 1rem 0.75rem
    line-height: 1.1578947
    +media-breakpoint-up(md)
      padding: 1.25rem 1.5rem
      font-size: 1.1875rem
    
    .fi
      display: inline-block
      line-height: 0
      font-size: 1.5rem
      cursor: pointer
      transition: $transition-base
      +hover-focus
        color: #29ac8f
  &-footer
    background: theme-color('light')
    color: $text-muted
    font-size: $font-size-sm
    padding: 0.25rem 0.75rem
    +media-breakpoint-up(md)
      padding: 0.75rem 1rem
      font-size: $font-size-base
  &-tags,
  &-btn
    padding: space(2) 0 0
  &-btn
    display: flex
    > *
      margin-bottom: space(2)
  &-tags
    border-top: 0.0625rem solid $border-color
    span
      display: inline-block
    +media-breakpoint-up(md)
      border-top: 0
      span
        margin-right: space(4)
  
  &-label
    display: block
    color: theme-color('theme')
    margin: 0 0 space(2)
    line-height: 1
    font:
      family: $font-family-source-sans
      size: $font-size-sm
    +media-breakpoint-up(md)
      font-size: $font-size-base

  &-title
    font:
      size: 1.125rem
      family: $font-family-source-sans
      weight: $font-weight-semibold
    margin: 0
    +media-breakpoint-up(md)
      font-size: $h4-font-size

.dropdown.show
  .btn-link
    position: relative

    background: $white
    box-shadow: $dropdown-box-shadow
    &:after
      display: block
      background: $white
      position: absolute
      content: ""
      left: -1*$border-width
      right: -1*$border-width
      bottom: -1*$border-width
      height: $dropdown-padding-y
      z-index: $zindex-dropdown + 1

.btn-search
  position: absolute
  left: 0
  top: 50%
  transform: translateY(-52%)
  font-size: 1.25rem
  +hover-focus
    text-decoration: none
  i
    display: block
    line-height: 1

.welcome
  position: relative
  margin-bottom: 50px
  font-size: 1.375rem
  z-index: 1
  a
    color: color('yellow')
    text-decoration: underline
    +hover-focus
      text-decoration: none
  &-footer
    z-index: -1
    text-align: center
    position: absolute
    left: 0
    right: 0
    top: 100%
    transition: $transition-base
    padding: 1.25rem 1rem
    font:
      family: $font-family-source-sans
      size: 1.125rem
  .inner-bg
    background-color: #6b3052
  
  label
    font:
      family: $font-family-source-sans
      size: 1.125rem

  .form-control
    border-color: $white
    color: $white
    background-color: theme-color('theme')
    font-weight: 900
    &:focus
      background-color: #954a76
    &::placeholder
      color: rgba($white, 0.5)
  


.btn-dark
  border-color: $white
  +hover-focus
    border-color: $white

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active
    border-color: $white
